import React, { useState } from "react";
import { Table } from "antd";
import PublicView from "../../components/PublicView";
import LEADERBOARD_LIST from "../../util/leaderboard";
import { LEADERBOARD_DATA, ALL_COLUMNS } from "./data";

import "./index.less";

function Leaderboard() {
  const [introduce, setIntroduce] = useState(LEADERBOARD_DATA[0].introduce);
  const [active, setActive] = useState(0);
  const [list, setList] = useState(LEADERBOARD_LIST[0]);
  const [columns, setColumns] = useState(ALL_COLUMNS[0]);

  const handleTabClick = (index) => {
    setActive(index);
    setIntroduce(LEADERBOARD_DATA[index].introduce);
    setList(LEADERBOARD_LIST[index]);
    setColumns(ALL_COLUMNS[index]);
  };

  return (
    <div className="leaderboard">
      <div className="white-layout">
        <div className="tabs">
          {LEADERBOARD_DATA.map((item, index) => (
            <div
              onClick={() => {
                handleTabClick(index);
              }}
              key={index}
              className={active === index ? "item-active tab" : "item tab"}
            >
              <div className="title">{item.title}</div>
              <div className="sub-title">{item.subTitle}</div>
            </div>
          ))}
        </div>
        <div className="introduce">{introduce}</div>

        <Table
          className="leaderboard-table"
          columns={columns}
          dataSource={list}
          pagination={false}
          scroll={{ x: active === 1 ? 1815 : 0 }}
        />
      </div>
    </div>
  );
}

export default PublicView(Leaderboard);
