import React, { useState } from "react";
import { Radio } from "antd";
import { FORMULA, FORMULA_LIST } from "./data";

import "./index.less";

export default function index() {
  const [code, setCode] = useState(FORMULA_LIST[0].formula);

  const handleRadioChange = (e) => {
    const active = e.target.value;
    setCode(FORMULA_LIST[active].formula);
  };

  return (
    <div className="citation white-layout">
      <div className="citation-title blue-tim-1">Citation</div>
      <div className="citation-describe blue-tim-3">
        Please cite our project as below if you use the RCVE benchmark.
      </div>
      <div className="formula">{FORMULA}</div>
      <div className="citation-describe blue-tim-3">
        We sincerely thank all dataset contributors to RCVE benchmark, please cite the following datasets if you use the
        RCVE benchmark.
      </div>
      <Radio.Group className="citation-radio-group" onChange={handleRadioChange} defaultValue={0} buttonStyle="solid">
        {FORMULA_LIST.map((item, index) => (
          <Radio.Button key={item.key} value={index}>
            {item.key}
          </Radio.Button>
        ))}
      </Radio.Group>
      <div className="code">{code}</div>
    </div>
  );
}
