import React, { useState } from "react";
import { Tabs } from "antd";
import { useNavigate, useLocation } from "react-router-dom";
import { MENU_LIST } from "../../util/menu";
import "./index.less";

export default function Head() {
  const navigate = useNavigate();
  const location = useLocation();
  const [activeKey, setActiveKey] = useState(location.pathname);

  const handleClickMenu = (key) => {
    if (key !== "code") {
      navigate(key);
    } else {
      window.open("https://github.com/alibaba/easyrobust", "_blank");
    }
    setActiveKey(key);
  };

  return (
    <div className="benchmark-head">
      <div className="head">
        <img
          className="image"
          src="https://img.alicdn.com/imgextra/i2/O1CN01oELxAq1oFoki9DX7Q_!!6000000005196-2-tps-786-246.png"
          alt="logo"
        />
        <div className="title">EasyRobust Benchmarks</div>
      </div>
      <Tabs activeKey={activeKey} onTabClick={handleClickMenu} tabBarGutter={17} className="menus" items={MENU_LIST} />
    </div>
  );
}
