import dataList from "./data/leaderboard_score.json";

const oodrob_imgcls_tasks = new Set([
  "ImageNet-val",
  "imagenet-v2",
  "imagenet-c",
  "imagenet-r",
  "imagenet-a",
  "imagenet-sketch",
  "stylized-imagenet",
  "objectnet",
]);
const advrob_imgcls_tasks = new Set(["ImageNet-val", "autoattack"]);
const advrob_objdet_tasks = new Set(["coco_test", "pgd_cls", "pgd_loc", "CWA"]);
const oodrob_objdet_tasks = new Set(["coco_test", "coco_c", "rpc"]);

const all_tasks = new Set([
  ...advrob_imgcls_tasks,
  ...oodrob_imgcls_tasks,
  ...advrob_objdet_tasks,
  ...oodrob_objdet_tasks,
]);

function compute_rank(scores) {
  let sorted = scores.slice().sort(function (a, b) {
    return b - a;
  });
  let ranks = scores.map(function (v) {
    return sorted.indexOf(v) + 1;
  });
  return ranks;
}

function compute_rank_break_tie(scores1, scores2) {
  let temp_list = [];
  scores1.forEach((s1, i) => {
    let temp = {};
    temp["s1"] = s1;
    temp["s2"] = scores2[i].toFixed(2);
    temp_list.push(temp);
  });
  let sorted = temp_list.slice().sort(function (a, b) {
    let aSize = a["s1"];
    let bSize = b["s1"];
    let aLow = a["s2"];
    let bLow = b["s2"];

    if (aSize == bSize) {
      return aLow < bLow ? 1 : aLow > bLow ? -1 : 0;
    } else {
      return aSize < bSize ? -1 : 1;
    }
  });
  let ranks = temp_list.map(function (v) {
    return sorted.indexOf(v) + 1;
  });
  return ranks;
}

function compute_mean_rank(data, tasks) {
  let tmp_mean_rank = new Array(data["model"].length).fill(0);
  for (let [key, value] of Object.entries(data)) {
    if (!key.includes("_rank")) {
      continue;
    }
    key = key.replace("_rank", "");
    if (tasks.has(key)) {
      for (let i = 0; i < value.length; i++) {
        tmp_mean_rank[i] += value[i] / tasks.size;
      }
    }
  }
  return tmp_mean_rank;
}

function add_value_into_key_array(data, key, value) {
  data[key] = data[key] || [];
  data[key].push(value);
}

function prepro_data(raw_data) {
  let advrob_parsed_data = {
    meta_ave: [],
  };
  let qa_parsed_data = {
    meta_ave: [],
  };
  let caption_parsed_data = {
    meta_ave: [],
  };
  let dddd_parsed_data = {
    meta_ave: [],
  };

  for (let i = 0; i < raw_data.length; i++) {
    let current_data = raw_data[i];
    let tmp_meta_ave_advrob = 0,
      tmp_meta_ave_dddd = 0;
    let tmp_meta_ave_qa = 0,
      tmp_meta_ave_caption = 0;
    for (let [key, value] of Object.entries(current_data)) {
      if (!all_tasks.has(key) && !key.includes("leaderboard")) {
        // add advrobust bench
        if (current_data["advrob_imgcls_leaderboard"]) {
          add_value_into_key_array(advrob_parsed_data, key, value);
        }
        if (current_data["oodrob_imgcls_leaderboard"]) {
          add_value_into_key_array(qa_parsed_data, key, value);
        }
        if (current_data["advrob_objdet_leaderboard"]) {
          add_value_into_key_array(caption_parsed_data, key, value);
        }
        if (current_data["oodrob_objdet_leaderboard"]) {
          add_value_into_key_array(dddd_parsed_data, key, value);
        }
      }

      if (advrob_imgcls_tasks.has(key)) {
        if (value) {
          if (current_data["advrob_imgcls_leaderboard"]) {
            tmp_meta_ave_advrob += value / advrob_imgcls_tasks.size;
            add_value_into_key_array(advrob_parsed_data, key, value);
          }
        }
      }
      if (oodrob_imgcls_tasks.has(key)) {
        if (value) {
          if (current_data["oodrob_imgcls_leaderboard"]) {
            if (!(key == "imagenet-c")) {
              tmp_meta_ave_qa += value / (oodrob_imgcls_tasks.size - 1);
            }
            add_value_into_key_array(qa_parsed_data, key, value);
          }
        }
      }
      if (advrob_objdet_tasks.has(key)) {
        if (value) {
          if (current_data["advrob_objdet_leaderboard"]) {
            tmp_meta_ave_caption += value / advrob_objdet_tasks.size;
            add_value_into_key_array(caption_parsed_data, key, value);
          }
        }
      }
      if (oodrob_objdet_tasks.has(key)) {
        if (value) {
          if (current_data["oodrob_objdet_leaderboard"]) {
            tmp_meta_ave_dddd += value / oodrob_objdet_tasks.size;
            add_value_into_key_array(dddd_parsed_data, key, value);
          }
        }
      }
    }

    if (current_data["advrob_imgcls_leaderboard"]) {
      advrob_parsed_data["meta_ave"].push(tmp_meta_ave_advrob);
    }
    if (current_data["oodrob_imgcls_leaderboard"]) {
      qa_parsed_data["meta_ave"].push(tmp_meta_ave_qa);
    }
    if (current_data["advrob_objdet_leaderboard"]) {
      caption_parsed_data["meta_ave"].push(tmp_meta_ave_caption);
    }
    if (current_data["oodrob_objdet_leaderboard"]) {
      dddd_parsed_data["meta_ave"].push(tmp_meta_ave_dddd);
    }
  }

  advrob_imgcls_tasks.forEach((key) => {
    let ranks = compute_rank(advrob_parsed_data[key]);
    advrob_parsed_data[key + "_rank"] = ranks;
  });

  for (let key of oodrob_imgcls_tasks) {
    if (!(key == "imagenet-c")) {
      let ranks = compute_rank(qa_parsed_data[key]);
      qa_parsed_data[key + "_rank"] = ranks;
    }
  }

  advrob_objdet_tasks.forEach((key) => {
    let ranks = compute_rank(caption_parsed_data[key]);
    caption_parsed_data[key + "_rank"] = ranks;
  });

  oodrob_objdet_tasks.forEach((key) => {
    let ranks = compute_rank(dddd_parsed_data[key]);
    dddd_parsed_data[key + "_rank"] = ranks;
  });

  advrob_parsed_data["mean_rank"] = compute_mean_rank(advrob_parsed_data, advrob_imgcls_tasks);
  advrob_parsed_data["global_rank"] = compute_rank_break_tie(
    advrob_parsed_data["mean_rank"],
    advrob_parsed_data["meta_ave"]
  );

  qa_parsed_data["mean_rank"] = compute_mean_rank(qa_parsed_data, oodrob_imgcls_tasks);
  qa_parsed_data["global_rank"] = compute_rank_break_tie(qa_parsed_data["mean_rank"], qa_parsed_data["meta_ave"]);

  caption_parsed_data["mean_rank"] = compute_mean_rank(caption_parsed_data, advrob_objdet_tasks);
  caption_parsed_data["global_rank"] = compute_rank_break_tie(
    caption_parsed_data["mean_rank"],
    caption_parsed_data["meta_ave"]
  );

  dddd_parsed_data["mean_rank"] = compute_mean_rank(dddd_parsed_data, oodrob_objdet_tasks);
  dddd_parsed_data["global_rank"] = compute_rank_break_tie(dddd_parsed_data["mean_rank"], dddd_parsed_data["meta_ave"]);

  return {
    advrob: advrob_parsed_data,
    qa: qa_parsed_data,
    caption: caption_parsed_data,
    dddd: dddd_parsed_data,
  };
}

function getArray(object) {
  return Object.keys(object).reduce(function (r, k) {
    object[k].forEach(function (a, i) {
      r[i] = r[i] || {};
      r[i][k] = a;
    });
    return r;
  }, []);
}

let all_data = prepro_data(dataList);

let advrob_data = getArray(all_data["advrob"]);
advrob_data.sort((a, b) => (a.global_rank > b.global_rank ? 1 : -1));

let qa_data = getArray(all_data["qa"]);
qa_data.sort((a, b) => (a.global_rank > b.global_rank ? 1 : -1));

let caption_data = getArray(all_data["caption"]);
caption_data.sort((a, b) => (a.global_rank > b.global_rank ? 1 : -1));

let dddd_data = getArray(all_data["dddd"]);
dddd_data.sort((a, b) => (a.global_rank > b.global_rank ? 1 : -1));

export default [advrob_data, qa_data, caption_data, dddd_data];
