import React, { useState } from "react";
import { Table } from "antd";
import PublicView from "../../components/PublicView";
import { TASK_LIST, COLUMNS } from "./data";

import "./index.less";

function Tasks() {
  const [active, setActive] = useState(0);
  const [list, setList] = useState(TASK_LIST[0].list);

  const handleTabClick = (index) => {
    setActive(index);
    setList(TASK_LIST[index].list);
  };

  const handleRowClassName = (record, index) => (index % 2 ? "table-even-row" : "table-odd-row");

  return (
    <div className="tasks">
      <div className="white-layout">
        <div className="head">
          <div className="tabs">
            {TASK_LIST.map((item, index) => (
              <div
                key={index}
                onClick={() => {
                  handleTabClick(index);
                }}
                className={active === index ? "item-active" : "item"}
              >
                {item.key}
              </div>
            ))}
          </div>
          <a
            className="download-btn"
            href="https://github.com/alibaba/easyrobust/blob/main/download_data.sh"
            target="_blank"
          >
            <img
              className="download"
              src="https://img.alicdn.com/imgextra/i4/O1CN01PoTVgE1lKRuL3ubi3_!!6000000004800-55-tps-39-39.svg"
              alt="下载"
            />
          </a>
        </div>

        <Table
          rowKey="name"
          className="task-table"
          rowClassName={handleRowClassName}
          columns={COLUMNS}
          dataSource={list}
          pagination={false}
        />
      </div>
    </div>
  );
}

export default PublicView(Tasks);
