import { MENU_LIST } from "../../util/menu";

export default (path) => {
  let result = {};
  MENU_LIST.forEach((item) => {
    if (item.key === path) {
      result = {
        title: item.title,
        describe: item.describe || "",
        bannerImage: item.banner,
      };
    }
  });
  return result;
};
